import express from 'express';
import expressJWT from 'express-jwt';
import env from '../../../config/env';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';
import getShipments from '../controllers/shipments_get';
import { baseUrl } from '../baseurl';

const router = express.Router();

/**
 * @swagger
 * /shipments:
 *   get:
 *     tags:
 *       - Shipments
 *     description: Returns list of Shipments
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "query"
 *         name: "search"
 *         description: "Search text"
 *         schema:
 *           type: object
 *           properties:
 *              search:
 *                  type: string
 *       - in: "query"
 *         name: "limit"
 *         description: "Sets the number of objects to be returned"
 *         schema:
 *           type: object
 *           properties:
 *              limit:
 *                  type: number
 *       - in: "query"
 *         name: "offset"
 *         description: "Sets the offset to the list of breakages"
 *         schema:
 *           type: object
 *           properties:
 *              offset:
 *                  type: number
 *       - in: "query"
 *         name: "from"
 *         description: "from date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *       - in: "query"
 *         name: "to"
 *         description: "to date"
 *         schema:
 *           type: object
 *           properties:
 *              from:
 *                  type: date
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *          type: array
 *          items:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            consignmentType:
 *              type: string
 *            destination:
 *              type: string
 *            vehicleNumber:
 *              type: String
 *            issueTime:
 *              type: date
 *            date:
 *              type: date
 *            packingList:
 *              type: string
 *            evc:
 *              type: string
 *            grn:
 *              type: string
 *            status:
 *              type: string
 */

/**
 * @swagger
 * /shipments:
 *   post:
 *     tags:
 *       - Shipments
 *     description: Returns success message if Shipments details are added
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *           type: object
 *           properties:
 *            consignmentType:
 *              type: string
 *            destination:
 *              type: string
 *            vehicleNumber:
 *              type: String
 *            issueTime:
 *              type: date
 *            date:
 *              type: date
 *            packingList:
 *              type: string
 *            evc:
 *              type: string
 *            grn:
 *              type: string
 *            status:
 *              type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            _id:
 *              type: string
 *            consignmentType:
 *              type: string
 *            destination:
 *              type: string
 *            vehicleNumber:
 *              type: String
 *            issueTime:
 *              type: date
 *            date:
 *              type: date
 *            packingList:
 *              type: string
 *            evc:
 *              type: string
 *            grn:
 *              type: string
 *            status:
 *              type: string
 */

/**
 * @swagger
 * /shipments:
 *   put:
 *     tags:
 *       - Shipments
 *     description: Returns success message if Shipments details are updated
 *     consumes:
 *       - application/json
 *     produces:
 *       - application/json
 *     parameters:
 *       - in: "body"
 *         name: "body"
 *         description: "Required details"
 *         required: true
 *         schema:
 *          type: array
 *          items:
 *            type: object
 *            properties:
 *              _id:
 *                type: string
 *              update:
 *                type: object
 *                properties:
 *                  consignmentType:
 *                    type: string
 *                  destination:
 *                    type: string
 *                  vehicleNumber:
 *                    type: String
 *                  issueTime:
 *                    type: date
 *                  date:
 *                    type: date
 *                  packingList:
 *                    type: string
 *                  evc:
 *                    type: string
 *                  grn:
 *                    type: string
 *                  status:
 *                    type: string
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *            message:
 *              type: string
 */

router.get(
  `${baseUrl}/`,
  expressJWT({
    secret: env.JWT_SECRET,
  }),
  getLoggedInUser,
  getShipments
);

export default router;
